import { Location } from '../types/weather';

export const getNWSForecastUrl = (location: Location): string => {
    // NWS uses a grid-based system, but for simplicity we'll use their point forecast API
    // which accepts lat/lng directly
    return `https://forecast.weather.gov/MapClick.php?lat=${location.lat}&lon=${location.lng}`;
};

export const shareLocation = async (location: Location, metric?: string): Promise<void> => {
    const url = new URL(window.location.href);
    url.searchParams.set('lat', location.lat.toString());
    url.searchParams.set('lng', location.lng.toString());
    url.searchParams.set('name', encodeURIComponent(location.name));
    if (metric) {
        url.searchParams.set('metric', metric);
    }

    try {
        await navigator.clipboard.writeText(url.toString());
    } catch (err) {
        console.error('Failed to copy to clipboard:', err);
        throw new Error('Failed to copy to clipboard');
    }
}; 