import React from 'react';
import { Box, Typography, ToggleButtonGroup, ToggleButton, IconButton, Tooltip } from '@mui/material';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import { useTheme } from '@mui/material';

interface WeatherHeaderProps {
    temperatureUnit: 'C' | 'F';
    onTemperatureUnitChange: (unit: 'C' | 'F') => void;
    onThemeToggle: () => void;
}

const WeatherHeader: React.FC<WeatherHeaderProps> = ({
    temperatureUnit,
    onTemperatureUnitChange,
    onThemeToggle
}) => {
    const theme = useTheme();

    return (
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
            <Typography variant="h4">
                Weather
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <ToggleButtonGroup
                    value={temperatureUnit}
                    exclusive
                    onChange={(_, newUnit) => newUnit && onTemperatureUnitChange(newUnit)}
                    size="small"
                >
                    <ToggleButton value="C">°C</ToggleButton>
                    <ToggleButton value="F">°F</ToggleButton>
                </ToggleButtonGroup>
                <Tooltip title={`Switch to ${theme.palette.mode === 'dark' ? 'light' : 'dark'} mode`}>
                    <span>
                        <IconButton onClick={onThemeToggle} color="inherit">
                            {theme.palette.mode === 'dark' ? <Brightness7Icon /> : <Brightness4Icon />}
                        </IconButton>
                    </span>
                </Tooltip>
            </Box>
        </Box>
    );
};

export default WeatherHeader; 