import React, { useMemo, useCallback, useState, useEffect } from 'react';
import { Box, FormControl, InputLabel, MenuItem, Select, useTheme, useMediaQuery, ToggleButtonGroup, ToggleButton } from '@mui/material';
import { ResponsiveContainer, LineChart, XAxis, YAxis, Tooltip, Line, TooltipProps, Legend, PieChart, Pie, Cell, ReferenceLine } from 'recharts';
import { Location, WeatherData } from '../types/weather';
import { formatTime, formatDayAndHour, formatHour, formatDate, isFuture } from '../utils/timeUtils';
import { Bolt as ThunderboltIcon } from '@mui/icons-material';
import {
    convertCloudCover,
    convertLiftedIndex,
    convertLiftedIndexBack,
    convertSeeing,
    convertSeeingBack,
    convertTransparency,
    convertTransparencyBack,
    convertRelativeHumidity,
    convertWindSpeed,
    convertPrecipitationAmount,
    convertSnowDepth
} from '../utils/plotDescriptions';

interface ChartDataItem extends WeatherData {
    wind_speed_10m: number;
    [key: `temp_profile_${number}`]: number;
    [key: `cloud_profile_${number}`]: number;
    [key: `rh_profile_${number}`]: number;
    [key: `wind_profile_${number}`]: number;
    processed_rh_profile?: number[];
}

interface WeatherTimePlotProps {
    data: WeatherData[];
    temperatureUnit: 'C' | 'F';
    date: Date;
    location: Location;
    selectedDate?: string;
    showHistorical?: boolean;
    onMetricChange?: (metric: string) => void;
    initialMetric?: string;
}

const CustomTooltip = ({ active, payload, label, data, selectedMetric, formattedDayAndHour, theme }: TooltipProps<any, any> & {
    data: WeatherData[],
    selectedMetric: string,
    formattedDayAndHour: Record<string, string>,
    theme: any
}) => {
    if (!active || !payload || !payload.length) return null;

    if (selectedMetric === 'wind_speed') {
        const timepoint = parseInt(label);
        const windSpeeds = data.find(d => d.timepoint === timepoint)?.wind_profile?.map(p => convertWindSpeed(p.speed)) || [];
        const avgWindSpeed = windSpeeds.reduce((a, b) => a + b, 0) / windSpeeds.length;
        return (
            <Box sx={{
                backgroundColor: 'background.paper',
                border: '1px solid',
                borderColor: 'divider',
                p: 1,
                borderRadius: 1
            }}>
                <div>{formattedDayAndHour[label] || ''}</div>
                <div>Average Wind Speed: {avgWindSpeed.toFixed(1)} m/s</div>
            </Box>
        );
    }

    if (selectedMetric === 'rh_profile') {
        const timepoint = parseInt(label);
        const humidities = data.find(d => d.timepoint === timepoint)?.rh_profile?.map(p => convertRelativeHumidity(p.rh)) || [];
        const avgHumidity = humidities.reduce((a, b) => a + b, 0) / humidities.length;
        return (
            <Box sx={{
                backgroundColor: 'background.paper',
                border: '1px solid',
                borderColor: 'divider',
                p: 1,
                borderRadius: 1
            }}>
                <div>{formattedDayAndHour[label] || ''}</div>
                <div>Average Humidity: {avgHumidity.toFixed(1)}%</div>
            </Box>
        );
    }

    const svgHeight = 60;
    const svgWidth = 60;

    if (selectedMetric === 'seeing') {
        const value = payload[0].value;
        const originalValue = convertSeeingBack(value);
        const maxRadius = 24;
        const minRadius = 6;
        const centerX = svgWidth / 2;
        const centerY = svgHeight / 2;

        // Calculate the foreground circle radius based on the seeing value
        const foregroundRadius = minRadius + (originalValue * (maxRadius - minRadius) / 8);

        return (
            <Box sx={{
                backgroundColor: 'background.paper',
                border: '1px solid',
                borderColor: 'divider',
                p: 1,
                borderRadius: 1
            }}>
                <div>{formattedDayAndHour[label] || ''}</div>
                <div>Seeing: {value.toFixed(2)}"</div>
                <svg width={svgWidth} height={svgHeight}>
                    {/* Background circle */}
                    <circle
                        cx={centerX}
                        cy={centerY}
                        r={maxRadius}
                        fill={theme.palette.mode === 'dark' ? '#000' : '#fff'}
                        stroke={theme.palette.mode === 'dark' ? '#666' : '#ccc'}
                        strokeWidth={0.5}
                    />
                    {/* Foreground circle */}
                    <circle
                        cx={centerX}
                        cy={centerY}
                        r={foregroundRadius}
                        fill={theme.palette.primary.main}
                        stroke={theme.palette.mode === 'dark' ? '#666' : '#ccc'}
                        strokeWidth={0.5}
                    />
                </svg>
            </Box>
        );
    }

    if (selectedMetric === 'transparency') {
        const value = payload[0].value;
        const originalValue = convertTransparencyBack(value);
        const totalBars = 8;
        const barHeight = (svgHeight - 8) / totalBars; // Leave 4px padding top and bottom
        const barWidth = svgWidth - 8; // Leave 4px padding left and right
        const bars = [];

        for (let i = 0; i < totalBars; i++) {
            const y = 4 + i * barHeight; // Start from 4px padding
            const isFilled = i >= (totalBars - originalValue);
            bars.push(
                <rect
                    key={i}
                    x={4} // Start from 4px padding
                    y={y}
                    width={barWidth}
                    height={barHeight - 1} // Leave 1px gap between bars
                    fill={isFilled
                        ? theme.palette.primary.main
                        : theme.palette.mode === 'dark' ? '#000' : '#fff'}
                    stroke={theme.palette.mode === 'dark' ? '#666' : '#ccc'}
                    strokeWidth={0.5}
                />
            );
        }

        return (
            <Box sx={{
                backgroundColor: 'background.paper',
                border: '1px solid',
                borderColor: 'divider',
                p: 1,
                borderRadius: 1
            }}>
                <div>{formattedDayAndHour[label] || ''}</div>
                <div>Transparency: {value.toFixed(2)}</div>
                <svg width={svgWidth} height={svgHeight}>
                    {bars}
                </svg>
            </Box>
        );
    }

    if (selectedMetric === 'cloudcover') {
        const value = payload[0].value;
        const data = [
            { name: 'Cloud Cover', value: value },
            { name: 'Clear', value: 100 - value }
        ];
        return (
            <Box sx={{
                backgroundColor: 'background.paper',
                border: '1px solid',
                borderColor: 'divider',
                p: 1,
                borderRadius: 1
            }}>
                <div>{formattedDayAndHour[label] || ''}</div>
                <div>Cloud Cover: {value.toFixed(1)}%</div>
                <ResponsiveContainer width={60} height={60}>
                    <PieChart>
                        <Pie
                            data={data}
                            cx="50%"
                            cy="50%"
                            innerRadius={0}
                            outerRadius={25}
                            paddingAngle={0}
                            dataKey="value"
                            stroke={theme.palette.mode === 'dark' ? '#666' : '#000'}
                            strokeWidth={1}
                        >
                            <Cell fill={theme.palette.mode === 'dark' ? '#ff4444' : '#ff0000'} />
                            <Cell fill={theme.palette.background.paper} />
                        </Pie>
                    </PieChart>
                </ResponsiveContainer>
            </Box>
        );
    }

    if (selectedMetric === 'lifted_index') {
        const value = payload[0].value;
        const originalValue = convertLiftedIndexBack(value);
        const thunderbolts = [];
        const totalThunderbolts = 3;
        const spacing = 8;
        const iconWidth = 16;
        const iconHeight = 16;
        for (let i = 0; i < totalThunderbolts; i++) {
            const x = (i * (iconWidth + spacing));
            const isFilled = (i === 0 && originalValue < 0) ||
                (i === 1 && originalValue < -3) ||
                (i === 2 && originalValue < -5);
            thunderbolts.push(
                <g key={i} transform={`translate(${x})`}>
                    <ThunderboltIcon
                        sx={{
                            width: iconWidth,
                            height: iconHeight,
                            color: isFilled ? theme.palette.primary.main : theme.palette.mode === 'dark' ? '#000' : '#fff',
                            stroke: theme.palette.mode === 'dark' ? '#666' : '#ccc',
                            strokeWidth: 0.5
                        }}
                    />
                </g>
            );
        }

        return (
            <Box sx={{
                backgroundColor: 'background.paper',
                border: '1px solid',
                borderColor: 'divider',
                p: 1,
                borderRadius: 1
            }}>
                <div>{formattedDayAndHour[label] || ''}</div>
                <div>Lifted Index: {value.toFixed(1)}</div>
                <svg width={svgWidth} height={svgHeight} style={{ overflow: 'visible' }}>
                    {thunderbolts}
                </svg>
            </Box>
        );
    }

    return (
        <Box sx={{
            backgroundColor: 'background.paper',
            border: '1px solid',
            borderColor: 'divider',
            p: 1,
            borderRadius: 1
        }}>
            <div>{formattedDayAndHour[label] || ''}</div>
            {payload.map((entry: any, index: number) => (
                <div key={index} style={{ color: entry.color }}>
                    {entry.name}: {entry.value}
                </div>
            ))}
        </Box>
    );
};

const WeatherTimePlot: React.FC<WeatherTimePlotProps> = ({ data, temperatureUnit, date, location, selectedDate, showHistorical, onMetricChange, initialMetric }) => {
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
    const [selectedMetric, setSelectedMetric] = React.useState(initialMetric || 'temp2m');
    const [formattedDayAndHour, setFormattedDayAndHour] = useState<Record<string, string>>({});
    const [currentTimeIndex, setCurrentTimeIndex] = useState<number | null>(null);
    const [timeRange, setTimeRange] = useState<'48h' | '96h' | 'all'>(() => {
        const savedRange = localStorage.getItem('timeRange');
        return (savedRange === '48h' || savedRange === '96h' || savedRange === 'all') ? savedRange : '48h';
    });

    // Update selectedMetric when initialMetric changes
    useEffect(() => {
        if (initialMetric) {
            setSelectedMetric(initialMetric);
        }
    }, [initialMetric]);

    // Notify parent component when metric changes
    useEffect(() => {
        onMetricChange?.(selectedMetric);
    }, [selectedMetric, onMetricChange]);

    // Save time range to localStorage whenever it changes
    useEffect(() => {
        localStorage.setItem('timeRange', timeRange);
    }, [timeRange]);

    // Update formatted times and find current time index when data changes
    useEffect(() => {
        const updateFormattedTimes = () => {
            const newFormattedTimes: Record<string, string> = {};
            const newFormattedHours: Record<string, string> = {};
            const newFormattedDayAndHour: Record<string, string> = {};
            const now = new Date();

            // Find the next valid forecast timepoint
            let nextTimepoint: number | null = null;
            let minFutureTimeDiff = Infinity;

            for (const item of data) {
                const time = formatTime(item.localDate);
                const hour = formatHour(item.localDate);
                const dayAndHour = formatDayAndHour(item.localDate);
                newFormattedTimes[item.timepoint] = time;
                newFormattedHours[item.timepoint] = hour;
                newFormattedDayAndHour[item.timepoint] = dayAndHour;

                // Only consider future timepoints
                if (isFuture(item.localDate)) {
                    const timeDiff = item.localDate.getTime() - now.getTime();
                    if (timeDiff < minFutureTimeDiff) {
                        minFutureTimeDiff = timeDiff;
                        nextTimepoint = item.timepoint;
                    }
                }
            }
            setFormattedDayAndHour(newFormattedDayAndHour);
            setCurrentTimeIndex(nextTimepoint);
        };

        updateFormattedTimes();
    }, [data, date, location]);

    const convertTemperature = useCallback((celsius: number): number => {
        if (temperatureUnit === 'F') {
            return (celsius * 9 / 5) + 32;
        }
        return celsius;
    }, [temperatureUnit]);

    // Add wind speed to the data
    const chartData: ChartDataItem[] = data.map(item => ({
        ...item,
        wind_speed_10m: item.wind10m.speed,
        temp2m: convertTemperature(item.temp2m)
    }));

    // Define categories using useMemo to prevent recalculation
    const categories = useMemo(() => {
        const baseCategories = [
            { value: 'temp2m', label: `Temperature (°${temperatureUnit})` },
            { value: 'prec_amount', label: 'Precipitation (mm)' },
            { value: 'cloudcover', label: 'Cloud Cover (%)' },
            { value: 'rh2m', label: 'Humidity 2M (%)' },
            { value: 'msl_pressure', label: 'Pressure (hPa)' },
            { value: 'seeing', label: 'Astronomical Seeing' },
            { value: 'transparency', label: 'Atmospheric Transparency' },
            { value: 'lifted_index', label: 'Lifted Index' },
            { value: 'snow_depth', label: 'Snow Depth (cm)' },
            { value: 'wind_speed_10m', label: 'Wind Speed 10M (m/s)' },
            { value: 'wind_speed', label: 'Wind Profile (m/s)' },
        ];

        // Add humidity profile option
        if (data[0]?.rh_profile) {
            baseCategories.push({
                value: 'rh_profile',
                label: 'Humidity Profile (%)'
            });
        }

        return baseCategories;
    }, [data, temperatureUnit]);

    // Process the data for the chart
    const processedChartData = useMemo(() => {
        const processed = [...chartData];

        // Convert API values to numeric values for plotting
        processed.forEach(item => {
            // Convert cloud cover
            if (typeof item.cloudcover === 'number') {
                item.cloudcover = convertCloudCover(item.cloudcover);
            }

            // Convert lifted index
            if (typeof item.lifted_index === 'number') {
                item.lifted_index = convertLiftedIndex(item.lifted_index);
            }

            // Convert seeing
            if (typeof item.seeing === 'number') {
                item.seeing = convertSeeing(item.seeing);
            }

            // Convert transparency
            if (typeof item.transparency === 'number') {
                item.transparency = convertTransparency(item.transparency);
            }

            // Convert relative humidity
            if (typeof item.rh2m === 'number') {
                item.rh2m = convertRelativeHumidity(item.rh2m);
            }

            // Convert wind speed
            if (typeof item.wind10m?.speed === 'number') {
                item.wind_speed_10m = convertWindSpeed(item.wind10m.speed);
            }

            // Convert precipitation amount
            if (typeof item.prec_amount === 'number') {
                item.prec_amount = convertPrecipitationAmount(item.prec_amount);
            }

            // Convert snow depth
            if (typeof item.snow_depth === 'number') {
                item.snow_depth = convertSnowDepth(item.snow_depth);
            }

            // Convert wind profile speeds
            if (item.wind_profile) {
                item.wind_profile.forEach((profile, i) => {
                    item[`wind_profile_${i}`] = convertWindSpeed(profile.speed);
                });
            }

            // Convert humidity profile
            if (item.rh_profile) {
                item.rh_profile.forEach((profile, i) => {
                    item[`rh_profile_${i}`] = convertRelativeHumidity(profile.rh);
                });
            }
        });

        return processed;
    }, [chartData]);

    // Get wind speed lines for the chart
    const windSpeedLines = useMemo(() => {
        if (selectedMetric !== 'wind_speed') return null;

        const lines = [];

        // Add wind profile lines with different shades of red
        if (data[0]?.wind_profile) {
            for (let i = 0; i < data[0].wind_profile.length; i++) {
                const layerLabel = data[0].wind_profile[i].layer || `${i + 1}km`;
                lines.push(
                    <Line
                        key={`wind_profile_${layerLabel}`}
                        type="monotone"
                        dataKey={`wind_profile_${i}`}
                        stroke={`rgb(${255 - i * 20}, ${i * 10}, ${i * 10})`}
                        name={layerLabel}
                        dot={false}
                    />
                );
            }
        }
        return lines;
    }, [data, selectedMetric]);

    // Get humidity profile lines for the chart
    const humidityProfileLines = useMemo(() => {
        if (selectedMetric !== 'rh_profile') return null;

        const lines = [];

        // Add humidity profile lines with different shades of red
        if (data[0]?.rh_profile) {
            for (let i = 0; i < data[0].rh_profile.length; i++) {
                const layerLabel = data[0].rh_profile[i].layer || `${i + 1}km`;
                lines.push(
                    <Line
                        key={`rh_profile_${layerLabel}`}
                        type="monotone"
                        dataKey={`rh_profile_${i}`}
                        stroke={`rgb(${255 - i * 20}, ${i * 10}, ${i * 10})`}
                        name={layerLabel}
                        dot={false}
                    />
                );
            }
        }
        return lines;
    }, [data, selectedMetric]);

    // Compute min and max timepoints for valid data
    const timeRangeData = useMemo(() => {
        const pointLimit = timeRange === '48h' ? 16 : timeRange === '96h' ? 32 : undefined;
        const validData = processedChartData.slice(0, pointLimit).filter(item => {
            if (selectedMetric === 'seeing' || selectedMetric === 'transparency') {
                return item[selectedMetric as keyof ChartDataItem] !== undefined &&
                    item[selectedMetric as keyof ChartDataItem] !== null;
            }
            if (selectedMetric === 'wind_speed') {
                return item.wind_profile && item.wind_profile.length > 0;
            }
            if (selectedMetric === 'rh_profile') {
                return item.rh_profile && item.rh_profile.length > 0;
            }
            return item[selectedMetric as keyof ChartDataItem] != null;
        });

        if (validData.length === 0) return [0, 0];

        return [
            Math.min(...validData.map(item => item.timepoint)),
            Math.max(...validData.map(item => item.timepoint))
        ];
    }, [processedChartData, selectedMetric, timeRange]);

    // Filter data based on timeRange
    const filteredData = useMemo(() => {
        return processedChartData.filter(item =>
            item.timepoint >= timeRangeData[0] && item.timepoint <= timeRangeData[1]
        );
    }, [processedChartData, timeRangeData]);

    return (
        <Box>
            <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel>Select</InputLabel>
                <Select
                    value={selectedMetric}
                    onChange={(e) => setSelectedMetric(e.target.value)}
                    label="Select"
                >
                    {categories.map((category) => (
                        <MenuItem key={category.value} value={category.value}>
                            {category.label}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>
            <ResponsiveContainer width="100%" height={400}>
                <LineChart
                    data={filteredData}
                    margin={{ top: 15, right: 10, left: 5, bottom: 30 }}
                >
                    <XAxis
                        dataKey="timepoint"
                        tickFormatter={(value: number) => formattedDayAndHour[value] || ''}
                        angle={-45}
                        textAnchor="end"
                        height={60}
                        interval="preserveStartEnd"
                    />
                    <YAxis
                        domain={['auto', 'auto']}
                        tickFormatter={(value: number) => {
                            if (selectedMetric === 'msl_pressure') {
                                return value.toFixed(1);
                            }
                            if (selectedMetric.startsWith('temp')) {
                                return value.toFixed(1);
                            }
                            return value.toString();
                        }}
                    />
                    {currentTimeIndex !== null && (
                        <>
                            <ReferenceLine
                                x={currentTimeIndex}
                                stroke={theme.palette.mode === 'dark' ? '#ff4444' : '#ff0000'}
                                strokeDasharray="3 3"
                                label={{
                                    value: 'Next',
                                    position: 'top',
                                    fill: theme.palette.mode === 'dark' ? '#ff4444' : '#ff0000',
                                    fontSize: 12
                                }}
                            />
                        </>
                    )}
                    <Tooltip
                        content={<CustomTooltip
                            data={data}
                            selectedMetric={selectedMetric}
                            formattedDayAndHour={formattedDayAndHour}
                            theme={theme}
                        />}
                    />
                    {(selectedMetric === 'wind_speed' || selectedMetric === 'rh_profile') && !isMobile && (
                        <Legend
                            verticalAlign="bottom"
                            height={36}
                            wrapperStyle={{
                                paddingBottom: '10px'
                            }}
                        />
                    )}
                    {selectedMetric === 'wind_speed' ? (
                        windSpeedLines
                    ) : selectedMetric === 'rh_profile' ? (
                        humidityProfileLines
                    ) : (
                        <Line
                            type="monotone"
                            dataKey={selectedMetric}
                            stroke={theme.palette.mode === 'dark' ? '#ff4444' : '#ff0000'}
                            name={categories.find(c => c.value === selectedMetric)?.label}
                            dot={(props) => {
                                const { cx, cy, payload } = props;
                                // Select points by the time that are in the past.
                                const isHistorical = currentTimeIndex !== null && payload.timepoint < currentTimeIndex;
                                // Select points by the date that are in the future.
                                const isDateSelected = selectedDate && formatDate(new Date(payload.localDate)) === selectedDate && payload.isFuture;
                                // If showHistorical is true, only show historical data.
                                // Otherwise, only show data for the selected date.
                                const isSelected = showHistorical ? isHistorical : isDateSelected;
                                return (
                                    <circle
                                        cx={cx}
                                        cy={cy}
                                        r={isSelected ? 6 : 4}
                                        fill={theme.palette.mode === 'dark' ? '#ff4444' : '#ff0000'}
                                        stroke={isSelected ? theme.palette.mode === 'dark' ? '#fff' : '#000' : 'none'}
                                        strokeWidth={isSelected ? 2 : 0}
                                    />
                                );
                            }}
                        />
                    )}
                </LineChart>
            </ResponsiveContainer>
            <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
                <ToggleButtonGroup
                    value={timeRange}
                    exclusive
                    onChange={(_, newRange) => newRange && setTimeRange(newRange)}
                    size="small"
                >
                    <ToggleButton value="48h">2 Days</ToggleButton>
                    <ToggleButton value="96h">4 Days</ToggleButton>
                    <ToggleButton value="all">All</ToggleButton>
                </ToggleButtonGroup>
            </Box>
        </Box>
    );
};

export default WeatherTimePlot; 