import React, { useState } from 'react';
import { Box, Paper, Tabs, Tab, Grid, Typography } from '@mui/material';
import WeatherTimePlot from './WeatherTimePlot';
import WeatherCard from './WeatherCard';
import WeatherFooter from './WeatherFooter';
import { Location, WeatherData } from '../types/weather';
import { formatDayOfWeek, formatDate } from '../utils/timeUtils';

interface WeatherDisplayProps {
    weatherResponse: {
        data: WeatherData[];
        date: Date;
    } | null;
    processedData: {
        groupedByDate: {
            past: Record<string, WeatherData[]>;
            future: Record<string, WeatherData[]>;
        };
        sortedPastDates: string[];
        sortedFutureDates: string[];
        totalPast: number;
        totalFuture: number;
    } | null;
    temperatureUnit: 'C' | 'F';
    location: Location;
    onMetricChange?: (metric: string) => void;
    initialMetric?: string;
}

const WeatherDisplay: React.FC<WeatherDisplayProps> = ({
    weatherResponse,
    processedData,
    temperatureUnit,
    location,
    onMetricChange,
    initialMetric
}) => {
    const [selectedDateIndex, setSelectedDateIndex] = useState(0);
    if (!weatherResponse || !processedData) return null;

    const handleDateChange = (event: React.SyntheticEvent, newValue: number) => {
        setSelectedDateIndex(newValue);
    };

    const historicalSelected = selectedDateIndex === processedData.sortedFutureDates.length;
    const selectedDate = historicalSelected ?
        processedData.sortedPastDates[0] :
        processedData.sortedFutureDates[selectedDateIndex];

    return (
        <>
            <Paper elevation={2} sx={{ p: 3, mb: 4 }}>
                <WeatherTimePlot
                    data={weatherResponse.data}
                    temperatureUnit={temperatureUnit}
                    date={weatherResponse.date}
                    location={location}
                    selectedDate={selectedDate}
                    showHistorical={historicalSelected}
                    onMetricChange={onMetricChange}
                    initialMetric={initialMetric}
                />
            </Paper>

            <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
                <Tabs
                    value={selectedDateIndex}
                    onChange={handleDateChange}
                    variant="scrollable"
                    scrollButtons="auto"
                    allowScrollButtonsMobile
                >
                    {processedData.sortedFutureDates.map((date: string) => (
                        <Tab
                            key={date}
                            label={
                                <Box>
                                    <Typography variant="body1">
                                        {formatDayOfWeek(new Date(date))}
                                    </Typography>
                                    <Typography variant="caption" color="text.secondary">
                                        {formatDate(new Date(date))}
                                    </Typography>
                                </Box>
                            }
                        />
                    ))}
                    {processedData.totalPast > 0 && (
                        <Tab
                            label={
                                <Box>
                                    <Typography variant="body1">
                                        Past
                                    </Typography>
                                    <Typography variant="caption" color="text.secondary">
                                        ({processedData.totalPast} card{processedData.totalPast !== 1 ? 's' : ''})
                                    </Typography>
                                </Box>
                            }
                        />
                    )}
                </Tabs>
            </Box>

            <Grid container spacing={3}>
                {historicalSelected ? (
                    // Show all past data, regardless of how far back in time it is.
                    processedData.sortedPastDates.flatMap((date: string) =>
                        processedData.groupedByDate.past[date].map((data: WeatherData) => (
                            <Grid item xs={12} sm={6} md={4} key={data.timepoint}>
                                <WeatherCard data={data} temperatureUnit={temperatureUnit} isPast={true} />
                            </Grid>
                        ))
                    )
                ) : (
                    processedData.groupedByDate.future[processedData.sortedFutureDates[selectedDateIndex]].map((data: WeatherData) => (
                        <Grid item xs={12} sm={6} md={4} key={data.timepoint}>
                            <WeatherCard data={data} temperatureUnit={temperatureUnit} />
                        </Grid>
                    ))
                )}
            </Grid>

            <WeatherFooter location={location} />
        </>
    );
};

export default WeatherDisplay; 