import React, { useState, useEffect } from 'react';
import { useQuery } from '@tanstack/react-query';
import {
    CircularProgress,
    Alert,
    Box,
    Snackbar,
} from '@mui/material';
import { fetchWeatherData, searchLocation } from '../api/weatherApi';
import { Location, WeatherData } from '../types/weather';
import { formatDate } from '../utils/timeUtils';
import { shareLocation } from '../utils/weatherUtils';
import { useTheme as useThemeContext } from '../context/ThemeContext';
import WeatherHeader from './WeatherHeader';
import WeatherSearch from './WeatherSearch';
import WeatherDisplay from './WeatherDisplay';

interface ProcessedWeatherData {
    groupedByDate: {
        past: Record<string, WeatherData[]>;
        future: Record<string, WeatherData[]>;
    };
    sortedPastDates: string[];
    sortedFutureDates: string[];
    totalPast: number;
    totalFuture: number;
}

const useProcessedWeatherData = (data: WeatherData[] | undefined, currentTime: Date): ProcessedWeatherData | null => {
    if (!data) return null;

    const groupedByDate = data.reduce((acc: {
        past: Record<string, WeatherData[]>;
        future: Record<string, WeatherData[]>;
        totalPast: number;
        totalFuture: number;
    }, data) => {
        const date = formatDate(data.localDate);
        const isPast = data.isCurrent || data.isPast || data.localDate < currentTime;
        const targetGroup = isPast ? acc.past : acc.future;

        if (!targetGroup[date]) {
            targetGroup[date] = [];
        }
        targetGroup[date].push(data);

        // Update totals
        if (isPast) {
            acc.totalPast++;
        } else {
            acc.totalFuture++;
        }

        return acc;
    }, { past: {}, future: {}, totalPast: 0, totalFuture: 0 });

    const sortedPastDates = Object.keys(groupedByDate.past).sort((a, b) => {
        return new Date(b).getTime() - new Date(a).getTime(); // Reverse order for past dates
    });
    const sortedFutureDates = Object.keys(groupedByDate.future).sort((a, b) => {
        return new Date(a).getTime() - new Date(b).getTime();
    });

    return {
        groupedByDate,
        sortedPastDates,
        sortedFutureDates,
        totalPast: groupedByDate.totalPast,
        totalFuture: groupedByDate.totalFuture
    };
};

const defaultLocation: Location = {
    lat: 47.6062,
    lng: -122.3321,
    name: 'Seattle, WA'
};

const Weather: React.FC = () => {
    const [location, setLocation] = useState<Location>(() => {
        const savedLocation = localStorage.getItem('lastLocation');
        if (savedLocation) {
            try {
                return JSON.parse(savedLocation);
            } catch (e) {
                console.error('Error parsing saved location:', e);
            }
        }
        return defaultLocation;
    });
    const [searchQuery, setSearchQuery] = useState('');
    const [temperatureUnit, setTemperatureUnit] = useState<'C' | 'F'>(() => {
        const savedUnit = localStorage.getItem('temperatureUnit');
        return (savedUnit === 'F' || savedUnit === 'C') ? savedUnit : 'F';
    });
    const [currentTime] = useState(new Date());
    const { toggleTheme } = useThemeContext();
    const [, setIsSearching] = useState(false);
    const [isGettingLocation, setIsGettingLocation] = useState(false);
    const [isRefreshing, setIsRefreshing] = useState(false);
    const [formattedLastUpdated, setFormattedLastUpdated] = useState<Date>(new Date());
    const [showShareSuccess, setShowShareSuccess] = useState(false);
    const [selectedMetric, setSelectedMetric] = useState<string>('temp2m');

    // Save location to localStorage whenever it changes
    useEffect(() => {
        localStorage.setItem('lastLocation', JSON.stringify(location));
    }, [location]);

    // Save temperature unit to localStorage whenever it changes
    useEffect(() => {
        localStorage.setItem('temperatureUnit', temperatureUnit);
    }, [temperatureUnit]);

    // Load location from URL parameters on initial load
    useEffect(() => {
        const params = new URLSearchParams(window.location.search);
        const lat = params.get('lat');
        const lng = params.get('lng');
        const name = params.get('name');
        const metric = params.get('metric');

        if (lat && lng) {
            setLocation({
                lat: parseFloat(lat),
                lng: parseFloat(lng),
                name: name ? decodeURIComponent(name) : 'Shared Location'
            });
        }
        if (metric) {
            setSelectedMetric(metric);
        }
    }, []);

    const { data: weatherResponse, isLoading: isLoadingWeather, error, refetch } = useQuery({
        queryKey: ['weather', location],
        queryFn: async () => {
            const { data, date } = await fetchWeatherData(location, true, true);
            return { data, date };
        },
        refetchInterval: 1000 * 60 * 60, // Refetch every hour
    });

    useEffect(() => {
        const updateFormattedTimes = async () => {
            if (!weatherResponse) return;

            // Update last updated time
            setFormattedLastUpdated(weatherResponse.date);
        };
        updateFormattedTimes();
    }, [weatherResponse, location]);

    const handleRefresh = async () => {
        setIsRefreshing(true);
        try {
            await refetch();
        } catch (error) {
            console.error('Error refreshing weather data:', error);
        } finally {
            setIsRefreshing(false);
        }
    };

    const handleGetCurrentLocation = () => {
        if (!navigator.geolocation) {
            alert('Geolocation is not supported by your browser. Please try searching for a location instead.');
            return;
        }

        setIsGettingLocation(true);
        navigator.geolocation.getCurrentPosition(
            (position) => {
                setLocation({
                    lat: position.coords.latitude,
                    lng: position.coords.longitude,
                    name: 'Current Location'
                });
                setIsGettingLocation(false);
                setSearchQuery(`${position.coords.latitude.toFixed(4)},${position.coords.longitude.toFixed(4)}`);
            },
            (error) => {
                console.error('Error getting location:', error);
                let errorMessage = 'Error getting your location. ';

                switch (error.code) {
                    case error.PERMISSION_DENIED:
                        errorMessage += 'Please enable location services in your browser settings and try again.';
                        break;
                    case error.POSITION_UNAVAILABLE:
                        errorMessage += 'Location information is unavailable. Please try searching for a location instead.';
                        break;
                    case error.TIMEOUT:
                        errorMessage += 'The request to get your location timed out. Please try again.';
                        break;
                    default:
                        errorMessage += 'Please try searching for a location instead.';
                }

                alert(errorMessage);
                setIsGettingLocation(false);
            },
            {
                enableHighAccuracy: true,
                timeout: 10000,
                maximumAge: 0
            }
        );
    };

    const handleSearch = async () => {
        if (!searchQuery.trim()) return;

        setIsSearching(true);
        try {
            const data = await searchLocation(searchQuery);

            if (data && data.length > 0) {
                setLocation({
                    lat: parseFloat(data[0].lat),
                    lng: parseFloat(data[0].lon),
                    name: data[0].display_name
                });
            } else {
                alert('Location not found. Please try a different search term.');
            }
        } catch (error) {
            console.error('Error geocoding location:', error);
            alert('Error searching for location. Please try again.');
        } finally {
            setIsSearching(false);
        }
    };

    const handleClearSearch = () => {
        setSearchQuery('');
    };

    const handleShare = async () => {
        try {
            await shareLocation(location, selectedMetric);
            setShowShareSuccess(true);
        } catch (error) {
            console.error('Error sharing location:', error);
            alert('Failed to copy location to clipboard');
        }
    };

    const processedData = useProcessedWeatherData(weatherResponse?.data, currentTime);

    return (
        <Box>
            <WeatherHeader
                temperatureUnit={temperatureUnit}
                onTemperatureUnitChange={setTemperatureUnit}
                onThemeToggle={toggleTheme}
            />

            <WeatherSearch
                searchQuery={searchQuery}
                onSearchQueryChange={setSearchQuery}
                onSearch={handleSearch}
                onClearSearch={handleClearSearch}
                onGetCurrentLocation={handleGetCurrentLocation}
                onShare={handleShare}
                onRefresh={handleRefresh}
                location={location}
                isGettingLocation={isGettingLocation}
                isRefreshing={isRefreshing}
                isLoadingWeather={isLoadingWeather}
                formattedLastUpdated={formattedLastUpdated}
            />

            <Snackbar
                open={showShareSuccess}
                autoHideDuration={3000}
                onClose={() => setShowShareSuccess(false)}
                message="Location link copied to clipboard"
            />

            {error ? (
                <Alert severity="error">
                    Error loading weather data: {error instanceof Error ? error.message : 'Unknown error'}
                </Alert>
            ) : isLoadingWeather ? (
                <Box display="flex" justifyContent="center" alignItems="center" minHeight="300px">
                    <CircularProgress />
                </Box>
            ) : (
                <WeatherDisplay
                    weatherResponse={weatherResponse || null}
                    processedData={processedData}
                    temperatureUnit={temperatureUnit}
                    location={location}
                    onMetricChange={setSelectedMetric}
                    initialMetric={selectedMetric}
                />
            )}
        </Box>
    );
};

export default Weather; 