import React from 'react';
import { Box, Grid, TextField, Typography, IconButton, Tooltip, CircularProgress } from '@mui/material';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import CloseIcon from '@mui/icons-material/Close';
import ShareIcon from '@mui/icons-material/Share';
import RefreshIcon from '@mui/icons-material/Refresh';
import { Location } from '../types/weather';

interface WeatherSearchProps {
    searchQuery: string;
    onSearchQueryChange: (query: string) => void;
    onSearch: () => void;
    onClearSearch: () => void;
    onGetCurrentLocation: () => void;
    onShare: () => void;
    onRefresh: () => void;
    location: Location;
    isGettingLocation: boolean;
    isRefreshing: boolean;
    isLoadingWeather: boolean;
    formattedLastUpdated: Date;
}

const WeatherSearch: React.FC<WeatherSearchProps> = ({
    searchQuery,
    onSearchQueryChange,
    onSearch,
    onClearSearch,
    onGetCurrentLocation,
    onShare,
    onRefresh,
    location,
    isGettingLocation,
    isRefreshing,
    isLoadingWeather,
    formattedLastUpdated
}) => {
    return (
        <Box sx={{ mb: 4 }}>
            <Grid container spacing={2} alignItems="center">
                <Grid item xs>
                    <TextField
                        fullWidth
                        label="Search Location"
                        value={searchQuery}
                        onChange={(e) => onSearchQueryChange(e.target.value)}
                        onKeyDown={(e) => e.key === 'Enter' && onSearch()}
                        placeholder="Enter a city, address, or landmark"
                        inputProps={{ enterKeyHint: 'search' }}
                        InputProps={{
                            startAdornment: (
                                <Tooltip title="Use Current Location">
                                    <span>
                                        <IconButton
                                            onClick={onGetCurrentLocation}
                                            disabled={isGettingLocation}
                                            color="primary"
                                            size="small"
                                            sx={{ mr: 1 }}
                                        >
                                            {isGettingLocation ? (
                                                <CircularProgress size={20} />
                                            ) : (
                                                <LocationOnIcon />
                                            )}
                                        </IconButton>
                                    </span>
                                </Tooltip>
                            ),
                            endAdornment: (
                                <>
                                    {searchQuery && (
                                        <Tooltip title="Clear Search">
                                            <IconButton
                                                onClick={onClearSearch}
                                                size="small"
                                                sx={{ mr: 1 }}
                                            >
                                                <CloseIcon />
                                            </IconButton>
                                        </Tooltip>
                                    )}
                                    <Tooltip title="Share Location">
                                        <IconButton
                                            onClick={onShare}
                                            size="small"
                                            sx={{ mr: 1 }}
                                        >
                                            <ShareIcon />
                                        </IconButton>
                                    </Tooltip>
                                </>
                            ),
                        }}
                    />
                </Grid>
            </Grid>
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                Current Location: (
                <a
                    href={`https://www.google.com/maps?q=${location.lat},${location.lng}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ color: 'inherit', textDecoration: 'underline' }}
                >
                    {location.name}
                </a>
                )
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Typography variant="body2" color="text.secondary">
                    Last updated: {formattedLastUpdated.toUTCString()}
                </Typography>
                <Tooltip title="Refresh Weather Data">
                    <span>
                        <IconButton
                            onClick={onRefresh}
                            disabled={isRefreshing || isLoadingWeather}
                            color="primary"
                            size="small"
                        >
                            {isRefreshing ? <CircularProgress size={20} /> : <RefreshIcon />}
                        </IconButton>
                    </span>
                </Tooltip>
            </Box>
        </Box>
    );
};

export default WeatherSearch; 